<template>
    <section>
        <div class="modal fade dialog my-popups ui-draggable show" style="height: auto; width: auto; display: block; top: 0px; left: 0px;" data-keyboard="false" data-backdrop="static" role="dialog">
            <div role="document" class="modal-dialog maxwidth500">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 id="myModalLabel" class="modal-title text-truncate pb-1">Upload a file </h5>
                        <span class="user-guide">
                            <a class="ancuserguide" data-toggle="collapse" href="javascript:;" role="button" aria-expanded="false"
                            aria-controls="collapseExample"> <em class="fa fa-files-o pr-2"></em>User Guide</a>
                            <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                            <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                <div class="con" v-html="$t('User_Guide_Dynamic_Manage_Folder')"></div>
                            </div>
                            </div>
                        </span>
                        <button type="button" data-dismiss="modal" class="close" v-on:click="closePopup"><i aria-hidden="true"
                                class="fa fa-times"></i></button>
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" ref="modalAddFile" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                            <template v-slot:tgslot-AttachmentFile="{ data }">
                                <a class="question-circle" href="javascript:;" data-toggle-tooltip="tooltip"
                                    :title="'Maximum upload size per file: ' + FileSizeAllowedToolTip + 'MB Allowed File Types: ' + FileTypeAllowedToolTip">
                                    <i class="fa fa-question-circle-o text-popover" style="font-size: 14px;"></i>
                                </a>
                                <div class="col-lg-12 pl-0">
                                    <div class="form-group">
                                        <div class="input-group">
                                            <div class="input-group">
                                                <div class="custom-file">
                                                    <input type="file" name="FileSrc1" id="flFile1" data-file="multi" tabindex="-1" style="position: absolute; clip: rect(0px, 0px, 0px, 0px);">
                                                    <div class="bootstrap-filestyle input-group">
                                                        <input type="text" disabled="disabled" class="form-control disabled">
                                                        <div class="input-group-append" style="cursor: pointer;">
                                                            <span tabindex="0" class="group-span-filestyle input-group-btn input-group-text bg-white choose-file">
                                                                <label title="Click here to select a file." for="flFile1" class="mb-0 font-weight-normal">Choose File</label>
                                                            </span>
                                                            <span class="input-group-text border-0 bg-transparent p-0 pl-2">
                                                                <a href="javascript:;" title="Clear" class="round-icon-small btn-danger-light">
                                                                    <i class="fa fa-undo"></i>
                                                                </a>
                                                            </span>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>                         
                            </template>
                            <template v-slot:tgslot-CheckBoxMultiple="{ data}">
                                <div class="form-group filterscroll" style="max-height:200px">
                                    <div class="custom-control custom-checkbox custom-control-inline w-100">
                                        <input type="checkbox" class="custom-control-input dynamic" v-on:change="onChannelChange()" id="'customcheckboxChannelType'+item.value" name="Channel" value="item.value" />
                                        <label class="custom-control-label" for="'customcheckboxChannelType'+item.value"></label>
                                    </div>
                                </div>
                            </template>
                        </dynamic-form>
                        <div class="py-2">
                            <span class="mandatory">Fields marked with an asterisk (*) are mandatory.</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>    
    </section>
</template>

<script>
import DataService from '@/services/DataService';
export default{
    data(){
        return{
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('FileTitle'),
                            name: "FileTitle",
                            value: "",
                            placeholder: "Enter File Title",
                            config: {
                                options: [],
                            },
                            validationRules: {
                                "required": true
                            },
                        },
                        {
                            astype: "SlotField",
                            label: this.$t('File'),
                            name: "AttachmentFile",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.onChangeDropdown
                            },
                            validationRules: "required",
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data:[
                        {
                            astype: "TextAreaField",
                            label: this.$t('Description'),
                            name: "Description",
                            value: "",
                            mode: "tag",
                            placeholder: "",
                            config: {
                                options: [],
                            },
                            validationRules: {
                                "required": true
                            },
                        }
                    ]
                },
                {
                    layoutType: "single",
                    group_name: 'Visible to',
                    group_display_name: this.$t('Visibleto'),
                    Data:[
                        {
                            astype: "SlotField",
                            label: '',
                            name: "CheckBoxMultiple",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.onChangeDropdown
                            },
                            validationRules: "",
                        }
                    ]
                }
            ],
            buttons:[
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-upload pr-2'></i>Upload"
                },
                {
                    type: "button",
                    class: "btn btn-primary delete-btn mr-2 my-sm-0",
                    text: "<i class='fa fa-times pr-2'></i>Cancel",
                    onClick: this.CancelUpload
                }
            ]
        }
    },
    created(){
        var vm = this;
        vm.GetChecboxUserData();
    },
    methods:{
        closePopup(){
            this.$parent.ShowManageFile = false;
        },
        CancelUpload(){
            var vm = this;
            debugger
        }
        // GetChecboxUserData(){
        //     var vm = this;
        //     debugger
        //     var params = `id=null&moduleName=PROJECT&type=CHANNEL&search=&refId=''`;
        //     DataService.GetChecboxData(params).then(response=>{
        //         debugger
        //     }).catch(err=>{
        //         console.log(err);
        //     })
        // }
    }
}
</script>
<style>
  #AttachmentFile {
   position:relative;
 }
 #AttachmentFile .input-group .question-circle{
    position:absolute;
    top:-24px;
    left: 34px;
 } 
 .custom-file .bootstrap-filestyle input{   
 padding-bottom: 7px;
    margin-bottom: 3.55px;
}
.custom-file .bootstrap-filestyle .choose-file{
    margin-bottom:4px;
}
</style>