<template lang="en">
<transition name="modal">
        <div class="modal d-block my-popups dialog">
        <div class="modal-dialog">
        <div class="modal-content">
        <div class="modal-header">
        <slot name="header">  </slot>
        <button type="button" title="" v-on:click="$emit('close')" class="close" data-original-title="Close"><i class="fa fa-times" aria-hidden="true"></i></button>
        </div>
        <div class="modal-body">
        <span class="user-guide">
        <a class="ancuserguide" data-toggle="collapse" href="#collapseReply" role="button" aria-expanded="false" aria-controls="collapseExample">User Guide</a>
        <div id="collapseReply" class="divancuserguide collapse">
        <div id="scrollbarreplyuserguide" class="custom-scrollbar-js">
        <div class="con" v-html="userGuide">{{userGuide}}</div>
        </div>
        </div>
        </span>
        <!-- v-slot="{ valid,errors}" -->
        <ValidationObserver ref="form" v-slot="{ handleSubmit }">
        <form  data-vv-scope="Note" v-on:submit.prevent="handleSubmit(saveNote)">
        <div class="d-flex flex-wrap">
        <div class="col-md-12 p-0">
        <div class="row">

        <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
        <div class="form-group">
            <ValidationProvider :rules="'required'" v-slot="{errors}">
        <label>Visibility<span class="text-danger">*</span></label>
        <!-- <select v-bind:class="{'form-control': true, 'is-invalid':errors.has('Note.Visibility')}" v-validate="'required'" name="Visibility" v-model="Visibility"> -->
            <!-- :rules="{'required':true}" v-slot="{errors,touched,invalid}" -->
        <select v-bind:class="{'form-control': true}" name="Visibility" v-model="Visibility">
        <option value="">Select</option>
        <option value="true">Public</option>
        <option value="false">Private</option>
        </select>
        <!-- <span v-show="errors.has('Note.Visibility')" class="invalid-feedback">{{ errors.first('Note.Visibility') }}</span> -->
      
        <span class="invalid-feedback d-block" v-if="errors[0]">{{ errors[0] }}</span>
    </ValidationProvider>
        </div>

        </div>

        <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left">
         <div class="form-group">
        <label>Task :</label></span>
        
        <select id="TaskDropdown" name="TaskDropdown" v-model="Task" v-bind:class="{'form-control bg-light': typeNoteTask == '','form-control bg-light disabled': typeNoteTask != ''}">
        <option value="">Select</option>
        <option v-for="tsk in TaskData" v-bind:value="tsk.TASK_ID">
         {{ tsk.TASK_NAME }}
        </option>
        </select>
       
        <div  class="invalid-feedback"></div>
        
        </div>
        </div>


        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
        <div class="form-group">
        <!-- <ValidationProvider :rules="'required'" v-slot="{errors}"> -->
        <label>Description<span class="text-danger">*</span></label>
        <!-- <ckeditor class="mb-0" v-bind:class="{'form-group': true, 'is-invalid':errors.has('Note.Description')}" name="Description" id="Description" maxlength="8000" v-model="Description" cols="20" v-validate="required"></ckeditor>
        <div v-show="errors.has('Note.Description')" class="invalid-feedback">{{ errors.first('Note.Description') }}</div> -->
        <!-- :rules="{'required':true}" -->
        <ckeditor :editor="editor" class="mb-0"  v-bind:class="{'form-group': true}" name="Description" id="Description" maxlength="8000" v-model="Description" cols="20"></ckeditor>
        <!-- <span class="invalid-feedback d-block" v-if="errors[0]">{{ errors[0] }}</span>
        </ValidationProvider> -->
        </div>
        </div>
        </div>
        <div class="row flex-row-reverse">
        <div class="col-lg-6 text-right">
        <button href="javascript:;" class="btn btn-success formbtn text-white mr-2 saveNote" type="submit"><i class="fa fa-save pr-2"></i>Save</button>
        <a v-on:click="$emit('close')" class="btn btn-danger formbtn widthhalf text-white"><i class="fa fa-close pr-2"></i>Cancel</a>
        </div>
        <div class="col-lg-6">
        <div class="py-2 mandatory">Fields marked with an asterisk (*) are mandatory.
        </div>
        </div>
        </div>
        </div>
        </div>
        </form>
        </ValidationObserver>
        </div>
        </div>
        </div>
        </div>
        </transition>
</template>
<script>
import DataService from '../../services/DataService';
import CKEditor from '@ckeditor/ckeditor5-vue2';
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import {
    ValidationProvider,
    extend,
    ValidationObserver
} from 'vee-validate';

extend('required', {
    validate(value) {
        return {
            required: true,
            valid: ['', null, undefined].indexOf(value) === -1
        };
    },
    message: '{_field_} is required',
    computesRequired: true
});
export default {
    name:'modalAddNote',
    components: { 
        ckeditor: CKEditor.component,
        ValidationProvider,
        ValidationObserver,
    },
props: {
        taskid: {
            type: String
        },
    },
    data: function () {
        return {
            noteId: null,
            Visibility: '',
            Task: '',
            Description: '',
            modal: null,
            isView: false,
            isPublic: false,
            userGuide: '',
            TaskData: [],
            typeNoteTask: '',
            editor:ClassicEditor
        }
    },
    created: function () {

        if (this.$parent.modalNoteItem != null && typeof this.$parent.modalNoteItem != 'undefined') {
            this.modal = this.$parent.modalNoteItem;
            this.noteId = this.$parent.modalNoteItem.NOTE_ID;
            this.Visibility = this.$parent.modalNoteItem.IS_PUBLIC;
            this.Task = this.$parent.modalNoteItem.TASK_ID;
            this.Description = this.$parent.modalNoteItem.NOTE_CONTENT;
            this.ProjectId = this.$parent.modalNoteItem.PROJECT_ID;

        }
        if (this.taskid != null) {
            this.Task = this.taskid;
            this.typeNoteTask = this.$parent.typeNoteTask;
        }


        this.userGuide = this.$parent.AddNoteUserGuide;
        this.TaskDropdown();
    },
    methods: {
        saveNote: function () {
             debugger;
            var vm = this;
            $(".saveNote").addClass("disabled");
             //vm.$validator.validateAll("Note").then(function (valid) {
                // this.$refs.addNoteForm.validate().then(function (valid) {
                //  if (valid) {
                    var data = {
                        noteId: vm.noteId == null ? 0 : vm.noteId,
                        description: vm.Description == null ? 0 : vm.Description,
                        isPublic: vm.Visibility == null ? false : vm.Visibility,
                        projectId: vm.$parent.ProjectId == null ? 0 : vm.$parent.ProjectId,
                        taskId: vm.Task == null ? null : vm.Task,
                        moduleName: vm.$parent.ModuleName,
                    };
                    var objectPostString = JSON.stringify(data);
                   DataService.saveNote(objectPostString).then(function (response) {
                    console.log(response.data.status)
                            if (response.data.status == "Success") {
                                debugger;
                                vm.ShowAlert(vm.$t("RecordsSavedSuccessfully"), 'success', true, vm.$t('Alert'));
                                vm.showAddNoteModal = false;
                                vm.$emit('close');
                                
                                vm.$parent.FetchData('kanban', null);
                                
                            }
                            vm.isLoading = false;
                        }, function (response) {
                            vm.isLoading = false;
                        });
        //         }
        //         else {
        //             $('#Fields_mandatory').show();
        //             $(".saveNote").removeClass("disabled");
        //         }
        //    });
        },
        TaskDropdown: function () {
            var vm = this;
            vm.isLoading = true;
            
            var params = "projectId=" + vm.$parent.ProjectId;
            DataService.LoadTaskByProjectId(params).then(function (response) {
                // debugger;
                vm.isLoading = false;
                if (response.data != null && response.data != "") {
                    vm.TaskData = response.data.result.DATA;
                }
            });
        }
    }
}

</script>